import React, { useState, useEffect } from 'react';
import {provider, getInstanceTriangleMasterChef,getInstanceTriangleToken,getInstanceTriangleNode} from '../Web3Util'

import Header from '../partials/Header';
import Footer from '../partials/Footer';
import Web3 from 'web3';

function Wallet() {
  let hasEthereum = window.ethereum != undefined
  const [contractInstanceMasterChef,contractUpdaterMasterChef] = useState(null)
  const [contractInstanceNode,contractUpdaterNode] = useState(null)
  const [contractInstanceToken,contractUpdaterToken] = useState(null)

  const [userTriangleBalance,userTriangleBalanceUpdater] = useState(0)
  const [avaxBalance,avaxBalanceUpdater] = useState(0)
  const [userNodeCounter,userNodeCounterUpdater] = useState(0)
  const [userNodeInfos,userNodeInfosUpdater] = useState([])
  const [userNodeItems, userNodeItemsUpdater] = useState([])


  useEffect(async ()  => {
   
    if(contractInstanceMasterChef == null){
      let contractInstanceMasterChefNew
      let contractInstanceNodeNew
      let contractInstanceTokenNew

      if(window.user != undefined){
        let web3 =   new Web3(window.ethereum);
        contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
        contractInstanceNodeNew = await getInstanceTriangleNode(web3)
        contractInstanceTokenNew = await getInstanceTriangleToken(web3)
      }else{
        let web3 =   new Web3(provider);
        contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
        contractInstanceNodeNew = await getInstanceTriangleNode(web3)
        contractInstanceTokenNew = await getInstanceTriangleToken(web3)
      }
      await contractUpdaterMasterChef(contractInstanceMasterChefNew)
      await contractUpdaterNode(contractInstanceNodeNew)
      await contractUpdaterToken(contractInstanceTokenNew)

      await loadData(contractInstanceMasterChefNew,contractInstanceNodeNew,contractInstanceTokenNew)
    }
  });

  async function stakeNode(nodeId){
    try{
      let web3 =   new Web3(window.ethereum);
      let contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      await contractInstanceMasterChefNew.methods.stakeNode(nodeId).send()
      window.location.reload()
    }catch(e){
      console.log("@@@ e",e)
    }
  }

  async function removeStakeNode(nodeId){
    try{
      let web3 =   new Web3(window.ethereum);
      let contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      await contractInstanceMasterChefNew.methods.unstakeNode(nodeId).send()
      window.location.reload()
    }catch(e){

    }
  }


  async function claimRewards(nodeId){
    try{
      let web3 =   new Web3(window.ethereum);
      let contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      await contractInstanceMasterChefNew.methods.claimRewards(nodeId).send()
      window.location.reload()
    }catch(e){
    }
  }


  async function loadData(contractInstanceMasterChef,contractInstanceNode,contractInstanceToken){
    if(window.user != null){
      let web3 =   new Web3(window.ethereum);

      const [userTriangleBalance,userNodeBalance,userCurrencyBalance] = await Promise.all([
            contractInstanceToken.methods.balanceOf(window.user).call(),
            contractInstanceNode.methods.balanceOf(window.user).call(),
            web3.eth.getBalance(window.user)
      ])
 
      let userNodeItems = []

      for(let counter=0;counter<userNodeBalance;++counter){
        let nodeID = await contractInstanceNode.methods.tokenOfOwnerByIndex(window.user,counter).call()
      
        const [level,status] = await Promise.all([
          contractInstanceNode.methods.nodeIdToLevel(nodeID).call(),
          contractInstanceNode.methods.stakingStatus(nodeID).call(),
        ])
        
        let rewards = 0
        if(status){
          rewards = await contractInstanceMasterChef.methods.calculateRewards(nodeID).call()
        }

        userNodeItems.push(
          <div key={nodeID} className="relative flex flex-col items-center p-6 bg-white rounded shadow-xl" data-aos="zoom-y-out" data-aos-delay="150">
            <img className="md:max-w-none mx-auto rounded " src={require('../images/nodes/'+level+'.png').default} width="250" height="250" alt="Features bg" />
            <br />
            <h4 className="text-xl font-bold leading-snug tracking-tight mb-1">Id - {nodeID}</h4>
            <h4 className="text-xl font-bold leading-snug tracking-tight mb-1">level - {level}</h4>
            {status=== true?  <>
              <a className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4" onClick={()=>removeStakeNode(nodeID)} >Remove stake</a> <br/>

              <a className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4" onClick={()=>claimRewards(nodeID)} >Claim {Number.parseInt(Web3.utils.fromWei(rewards+"", 'ether')).toFixed(5)} $TRIANGLE</a> 


            </>: <>
            <a className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4" onClick={()=>stakeNode(nodeID)} >Stake node</a> 

            </>}
          </div>
        )
      }


      userNodeItemsUpdater(userNodeItems)

      userTriangleBalanceUpdater(userTriangleBalance)
      avaxBalanceUpdater(userCurrencyBalance)
      userNodeCounterUpdater(userNodeBalance)
    }
    
  }


  return (
    <div className="flex flex-col min-h-screen overflow-hidden">

      {/*  Site header */}
      <Header  hasEthereum={hasEthereum} />

      {/*  Page content */}
      <main className="flex-grow">

        {/*  Page sections */}
        
        {/* <WalletBlocks bears={userNFTs}/> */}

        <section className="relative">
          {/* Section background (needs .relative class on parent and next sibling elements) */}
          <div className="absolute inset-0 top-1/2 md:mt-24 lg:mt-0 bg-gray-900 pointer-events-none" aria-hidden="true"></div>
          {/* <div className="absolute left-0 right-0 bottom-0 m-auto w-px p-px h-20 bg-gray-200 transform translate-y-1/2"></div> */}

          <div className="relative max-w-6xl mx-auto px-4 sm:px-6">
            <div className="py-12 md:py-20">

              {/* Section header */}
              <div className="max-w-3xl mx-auto text-center pb-12 md:pb-20">
                <h2 className="h2 mb-4">My wallet</h2>
                <p className="text-xl text-gray-600">View your $TRIANGLE balance and manage your nodes.</p>
              </div>

              {/* Items */}
              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none">
                
                <p className="text-xl text-gray-600" style={{textAlign:"center"}}>$TRIANGLE balance: <br/>{Web3.utils.fromWei(userTriangleBalance+"","ether")}</p>
                <p className="text-xl text-gray-600" style={{textAlign:"center"}}>$AVAX balance: <br/>{Web3.utils.fromWei(avaxBalance+"","ether")}</p>
                <p className="text-xl text-gray-600" style={{textAlign:"center"}}>Triangle node balance: <br/>{userNodeCounter}</p>

              </div>

              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none">
                
                {userNodeItems}
               
              </div>


            </div>
          </div>
        </section>

      </main>

      {/*  Site footer */}
      <Footer />

    </div>
  );
}

export default Wallet;