import Web3 from 'web3';
import TriangleTokenABI from './abi/triangletoken.json'
import TriangleNodeABI from './abi/trianglenode.json'
import TriangleMasterChefABI from './abi/trianglemasterchef.json'

let isTestnet = true

// main net

let addressTriangleToken = ""
let addressTriangleNode = ""
let addressTriangleMasterChef = ""
let providerTemp = new Web3.providers.HttpProvider("https://api.avax.network/ext/bc/C/rpc"); 

// testnet 
if(isTestnet){
    addressTriangleToken = "0x2a43696fC2D9de24B9263b2B2bDAA0bC7f0D28fC"
    addressTriangleNode = "0xF6ECf5E040eC12daAf74Fc190b7926a69D28631A"
    addressTriangleMasterChef = "0x11caE626719f6d5cF553F2245911987d13a9Be94"
    providerTemp = new Web3.providers.HttpProvider("https://api.avax-test.network/ext/bc/C/rpc"); 
}

export  const provider = providerTemp
export const addressMaster = addressTriangleMasterChef

export const getWeb3 = () =>
    new Promise(async(resolve, reject) => {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
            // await window.ethereum.enable();
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            resolve(web3);
        } catch (error) {
        reject(error);
        }
    }
    else {
 
        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        resolve(web3);
    }
});

export const getUser = async () => {
    if (window.ethereum) {
        let web3 =   new Web3(window.ethereum);

         window.user = (await web3.eth.getAccounts())[0];

    }
    return window.user
}


export const getInstanceTriangleToken = async (web3) => {
    window.user = await getUser();

    window.instanceTriangleToken= new web3.eth.Contract(
        TriangleTokenABI,
        addressTriangleToken,
        {
            from: window.user
        }
    );
    return window.instanceTriangleToken;
}

export const getInstanceTriangleNode = async (web3) => {
    window.user = await getUser();
    
    window.instanceTriangleNode= new web3.eth.Contract(
        TriangleNodeABI,
        addressTriangleNode,
        {
            from: window.user
        }
    );
    return window.instanceTriangleNode;
}

export const getInstanceTriangleMasterChef = async (web3) => {
    window.user = await getUser();
    
    window.instanceTriangleMasterChef= new web3.eth.Contract(
        TriangleMasterChefABI,
        addressTriangleMasterChef,
        {
            from: window.user
        }
    );
    return window.instanceTriangleMasterChef;
}