import React, { useState, useEffect } from 'react';
import {provider, getInstanceTriangleMasterChef, getInstanceTriangleToken,addressMaster} from '../Web3Util'
import Web3 from 'web3';

function FeaturesBlocks() {

  // begin triangle stuff 
  const [contractInstanceMasterChef,contractUpdaterMasterChef] = useState(null)
  const [componentItems, itemsUpdater] = useState([])

  useEffect(async ()  => {
    if(contractInstanceMasterChef == null){
      let contractInstanceMasterChefNew
      if(window.user != undefined){
        let web3 =   new Web3(window.ethereum);
        contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      }else{
        let web3 =   new Web3(provider);
        contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      }
      await contractUpdaterMasterChef(contractInstanceMasterChefNew)
      await loadData(contractInstanceMasterChefNew)
    }
  });

  async function mintWithAVAX(nodeLevel,price){
    try{
      let web3 =   new Web3(window.ethereum);
      let contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
    
      await contractInstanceMasterChefNew.methods.buyNodeForAVAX(nodeLevel).send({
        value:price
      })
      window.location.reload()
    }catch(e){
      // console.log("@@@ e",e)
    }
  }

  async function mintWithTriangle(nodeLevel){
    try{
      
      let web3 =   new Web3(window.ethereum);
      let contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      await contractInstanceMasterChefNew.methods.buyNodeForTriangles(nodeLevel).send()
      window.location.reload()
    }catch(e){
      // console.log("@@@ e",e)
    }
  }

  async function approveTriangles(){
    try{
      
      let web3 =   new Web3(window.ethereum);
      let contractInstanceTokenNew = await getInstanceTriangleToken(web3)
      await contractInstanceTokenNew.methods.approve(addressMaster,"100000000000000000000000").send()
      window.location.reload()
    }catch(e){
      // console.log("@@@ e",e)
    }
  }

  async function loadData(contractInstanceMasterChef){
    
    const pricesInTriangle = await Promise.all([
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(0).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(1).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(2).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(3).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(4).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(5).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(6).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(7).call(),
      contractInstanceMasterChef.methods.NODE_INITIAL_PRICES(8).call(),
    ])

    const pricesInAVAX = await Promise.all([
      contractInstanceMasterChef.methods.getNodePriceAVAX(1).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(2).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(3).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(4).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(5).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(6).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(7).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(8).call(),
      contractInstanceMasterChef.methods.getNodePriceAVAX(9).call(),
    ])

    const rewardsBase = await Promise.all([
      contractInstanceMasterChef.methods.REWARDS_BASE(0).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(1).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(2).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(3).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(4).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(5).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(6).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(7).call(),
      contractInstanceMasterChef.methods.REWARDS_BASE(8).call(),
    ])

    const nodesMintedByLevel = await Promise.all([
      contractInstanceMasterChef.methods.nodesMinted(1).call(),
      contractInstanceMasterChef.methods.nodesMinted(2).call(),
      contractInstanceMasterChef.methods.nodesMinted(3).call(),
      contractInstanceMasterChef.methods.nodesMinted(4).call(),
      contractInstanceMasterChef.methods.nodesMinted(5).call(),
      contractInstanceMasterChef.methods.nodesMinted(6).call(),
      contractInstanceMasterChef.methods.nodesMinted(7).call(),
      contractInstanceMasterChef.methods.nodesMinted(8).call(),
      contractInstanceMasterChef.methods.nodesMinted(9).call(),
    ])

    let web3 =   new Web3(window.ethereum);
    let contractInstanceTokenNew = await getInstanceTriangleToken(web3)
    let allowance = await contractInstanceTokenNew.methods.allowance(window.user,addressMaster).call()
     
    console.log("@@@ allowance",allowance)

    let items =[]

    for(let counter=0;counter<9;++counter){
      items.push(
        <div className="relative flex flex-col items-center p-6 bg-white rounded shadow-xl" data-aos="zoom-y-out" data-aos-delay="150">
          <h4 className="text-xl font-bold leading-snug tracking-tight mb-1">Node level {(Number.parseInt(counter)+1)}</h4>
          <img className="md:max-w-none mx-auto rounded " src={require('../images/nodes/'+(Number.parseInt(counter)+1)+'.png').default} width="150" height="150" alt="Features bg" />
          <p className="text-gray-600 text-center"><b>Node cost</b> <br/>{Web3.utils.fromWei(pricesInTriangle[counter]+"", 'ether')} $TRIANGLE tokens or <br/>{Web3.utils.fromWei(pricesInAVAX[counter]+"", 'ether')} $AVAX</p> <br/>
          <p className="text-gray-600 text-center"><b>Stakig rewards</b>  <br/>Starting at {Web3.utils.fromWei(rewardsBase[counter]+"", 'ether')} $TRIANGLE / day (decaying over time)</p> <br/>
          <p className="text-gray-600 text-center"><b>Nodes minted</b>  <br/>{nodesMintedByLevel[counter]}</p> <br/>

          <p className="text-gray-600 text-center">
            
          {allowance != "100000000000000000000000"?
            <><a className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4" onClick={()=>approveTriangles()} >Mint using $TRIANGLE *approve first</a> <br/><br/></>
            :
            <><a className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4" onClick={()=>mintWithTriangle(Number.parseInt(counter)+1)} >Mint using $TRIANGLE</a> <br/><br/></>
          }
          <a className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4" onClick={()=>mintWithAVAX(Number.parseInt(counter)+1,pricesInAVAX[counter])} >Mint using $AVAX</a>

          
          </p> <br/>

        
        </div>
      )
    }
    await itemsUpdater(items)
  }
  // end triangle stuff 

  

  return (
    <section className="relative">

      {/* Section background (needs .relative class on parent and next sibling elements) */}
      <div className="absolute inset-0 top-1/2 md:mt-24 lg:mt-0 bg-gray-900 pointer-events-none" aria-hidden="true"></div>
      <div className="absolute left-0 right-0 bottom-0 m-auto w-px p-px h-20 bg-gray-200 transform translate-y-1/2"></div>

      <div className="relative max-w-6xl mx-auto px-4 sm:px-6" >
        <div className="py-12 md:py-20" >

          {/* Section header */}
          <div className="max-w-3xl mx-auto text-center pb-12 md:pb-20">
            <h2 className="h2 mb-4">Triangle Nodes</h2>
            <p className="text-xl text-gray-600">It is possible to burn $TRIANGLE tokens in exchange for a node. Nodes have different levels based on the amount of $TRIANGLE tokens burned. Nodes can be staked. When staked, they yield rewards in $TRIANGLE tokens.</p>
          </div>

          {/* Items */}
          <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none" style={{minHeight:"200px"}}>

            {componentItems}
           
          </div>

        </div>
      </div>
    </section>
  );
}

export default FeaturesBlocks;
