import React, { useState, useRef, useEffect } from 'react';
import Transition from '../utils/Transition.js';
import {provider, getInstanceTriangleMasterChef} from '../Web3Util'
import Web3 from 'web3';

function Features() {
  const [contractInstanceMasterChef,contractUpdaterMasterChef] = useState(null)
  const [rateIncrease, rateIncreaseUpdater] = useState(0)

  useEffect(async ()  => {
    if(contractInstanceMasterChef == null){
      let contractInstanceMasterChefNew
      if(window.user != undefined){
        let web3 =   new Web3(window.ethereum);
        contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      }else{
        let web3 =   new Web3(provider);
        contractInstanceMasterChefNew = await getInstanceTriangleMasterChef(web3)
      }
      await contractUpdaterMasterChef(contractInstanceMasterChefNew)
      await loadData(contractInstanceMasterChefNew)
    }
  });

  async function loadData(contractInstanceMasterChef){
    const [priceIncreaseRate] = await Promise.all([
      contractInstanceMasterChef.methods.priceIncreaseRate().call(),
    ])

    rateIncreaseUpdater(priceIncreaseRate)
  }

  const [tab, setTab] = useState(1);

  const tabs = useRef(null);

  const heightFix = () => {
    if (tabs.current.children[tab]) {
      tabs.current.style.height = tabs.current.children[tab - 1].offsetHeight + 'px'
    }
  }

  useEffect(() => {
    heightFix()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tab])

  return (
    <section className="relative">

      {/* Section background (needs .relative class on parent and next sibling elements) */}
      <div className="absolute inset-0 bg-gray-100 pointer-events-none mb-16" aria-hidden="true"></div>
      <div className="absolute left-0 right-0 m-auto w-px p-px h-20 bg-gray-200 transform -translate-y-1/2"></div>

      <div className="relative max-w-6xl mx-auto px-4 sm:px-6">
        <div className="pt-12 md:pt-20">

          {/* Section header */}
          <div className="max-w-3xl mx-auto text-center pb-12 md:pb-16">
            <h1 className="h2 mb-4">Triangle Finance</h1>
            <p className="text-xl text-gray-600">The new generation yield farming on the blockchain.</p>
          </div>

          {/* Section content */}
          <div className="md:grid md:grid-cols-12 md:gap-6">

            {/* Content */}
            <div className="max-w-xl md:max-w-none md:w-full mx-auto md:col-span-7 lg:col-span-6 md:mt-6" data-aos="fade-right">
              <div className="md:pr-4 lg:pr-12 xl:pr-16 mb-8">
                <h3 className="h3 mb-3">Earn $TRIANGLE by staking your nodes</h3>
                {/* <p className="text-xl text-gray-600">Lazy Bear holders that LP in the Frozen Capital dapp have a chance to earn airdrops. The more NFTs you hold, the more likely you are to receive airdrops.</p> */}
              </div>
              {/* Tabs buttons */}
              <div className="mb-8 md:mb-0">
                <a
                  className={`flex items-center text-lg p-5 rounded border transition duration-300 ease-in-out mb-3 ${tab !== 1 ? 'bg-white shadow-md border-gray-200 hover:shadow-lg' : 'bg-gray-200 border-transparent'}`}
                  href="#0"
                  onClick={(e) => { e.preventDefault(); setTab(1); }}
                >
                  <div>
                    <div className="font-bold leading-snug tracking-tight mb-1">$TRIANGLE tokens</div>
                    <div className="text-gray-600">$TRIANGLE are erc20 tokens that can be obtained at a DEX or through staking a Triangle node.</div>
                  </div>
                </a>
                <a
                  className={`flex items-center text-lg p-5 rounded border transition duration-300 ease-in-out mb-3 ${tab !== 2 ? 'bg-white shadow-md border-gray-200 hover:shadow-lg' : 'bg-gray-200 border-transparent'}`}
                  href="#0"
                  onClick={(e) => { e.preventDefault(); setTab(2); }}
                >
                  <div>
                    <div className="font-bold leading-snug tracking-tight mb-1">Triangle nodes</div>
                    <div className="text-gray-600">Triangle nodes are erc721 tokens that can be obtained on this site, by burning $TRIANGLE or directly by trading $AVAX for them. The node price in $TRIANGLE never changes, but the price in $AVAX increases by {rateIncrease}% every day once the node staking begins. </div>
                  </div>
                </a>
                <a
                  className={`flex items-center text-lg p-5 rounded border transition duration-300 ease-in-out mb-3 ${tab !== 3 ? 'bg-white shadow-md border-gray-200 hover:shadow-lg' : 'bg-gray-200 border-transparent'}`}
                  href="#0"
                  onClick={(e) => { e.preventDefault(); setTab(3); }}
                >
                  <div>
                    <div className="font-bold leading-snug tracking-tight mb-1">Node staking</div>
                    <div className="text-gray-600">You can stake your triangle nodes (erc721 tokens) in this site in order to farm $TRIANGLE tokens with high APR. The higher level your node is, the more $TRIANGLE rewards are earned. The $TRIANGLE rewards for a node diminish with time.</div>
                  </div>
                </a>
              </div>
            </div>

            {/* Tabs items */}
            <div className="max-w-xl md:max-w-none md:w-full mx-auto md:col-span-5 lg:col-span-6 mb-8 md:mb-0 md:order-1" data-aos="zoom-y-out" ref={tabs}>
              <div className="relative flex flex-col text-center lg:text-right">
                {/* Item 1 */}
                
                <Transition
                  show={tab === 1}
                  appear={true}
                  className="w-full"
                  enter="transition ease-in-out duration-700 transform order-first"
                  enterStart="opacity-0 translate-y-16"
                  enterEnd="opacity-100 translate-y-0"
                  leave="transition ease-in-out duration-300 transform absolute"
                  leaveStart="opacity-100 translate-y-0"
                  leaveEnd="opacity-0 -translate-y-16"
                >
                  <div className="relative inline-flex flex-col">
                    <img className="md:max-w-none mx-auto rounded transform animate-float" src={require('../images/nodes/1.png').default} width="600" height="462" alt="Features bg" />
                  </div>
                </Transition>
                {/* Item 2 */}
                <Transition
                  show={tab === 2}
                  appear={true}
                  className="w-full"
                  enter="transition ease-in-out duration-700 transform order-first"
                  enterStart="opacity-0 translate-y-16"
                  enterEnd="opacity-100 translate-y-0"
                  leave="transition ease-in-out duration-300 transform absolute"
                  leaveStart="opacity-100 translate-y-0"
                  leaveEnd="opacity-0 -translate-y-16"
                >
                  <div className="relative inline-flex flex-col">
                    <img className="md:max-w-none mx-auto rounded transform animate-float" src={require('../images/nodes/2.png').default} width="600" height="462" alt="Features bg" />
                  </div>
                </Transition>

                <Transition
                  show={tab === 3}
                  appear={true}
                  className="w-full"
                  enter="transition ease-in-out duration-700 transform order-first"
                  enterStart="opacity-0 translate-y-16"
                  enterEnd="opacity-100 translate-y-0"
                  leave="transition ease-in-out duration-300 transform absolute"
                  leaveStart="opacity-100 translate-y-0"
                  leaveEnd="opacity-0 -translate-y-16"
                >
                  <div className="relative inline-flex flex-col">
                    <img className="md:max-w-none mx-auto rounded transform animate-float" src={require('../images/nodes/3.png').default} width="600" height="462" alt="Features bg" />
                  </div>
                </Transition>
              </div>
            </div >

          </div >

        </div >
      </div >
    </section >
  );
}

export default Features;
